
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { Header } from "@/shared/type/service-header";
import { defineComponent } from "vue";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const';

export default defineComponent({
    components: { Pagination, VueDatePicker },
    data() {
        return {
            schedulePaymentReportData: [] as SchedulePaymentReportDataType[],
            COMMON_KEY_VALUE,
            header: [
                { columnName: 'No' },
                { columnName: 'Reference No' },
                { columnName: 'Biz CID' },
                { columnName: 'Biz Name' },
                { columnName: 'From Account No' },
                { columnName: 'Number of Cheque Book' },
                { columnName: 'Number of Leaves' },
                { columnName: 'Fee Charge' },
                { columnName: 'Remark' },
                { columnName: 'Authorization Status' },
                { columnName: 'Create By' },
                { columnName: 'Create Date' },
                { columnName: 'Authorization by' },
                { columnName: 'Authorization Date' },
                { columnName: 'Authorization  Status (Bakend)' },
                { columnName: 'Verified By ' },
                { columnName: 'Verified Date' },
                { columnName: 'Authorization By (bakend)' },
                { columnName: 'Authorization Date (bakend)' },
            ],
            totalItems: 0,
            pageSize: PAGE_SIZE,
            currentPage: 1,
            startingIndex: 1,
            isResetPage: false,
            searchValueBy: {
                date: new Date()
            }
        }
    },
    mounted() {
        this.getOpenFixDepositReportData();
    },
    computed: {
        formattedDate(): string {
            return this.$datetime.formatVueDatePicker(this.searchValueBy.date)
        },
    },
    methods: {
        async getOpenFixDepositReportData() {
            const requestBody: RPT202001SS08_REQ_BODY = {
                pageNumber: this.currentPage,
                pageSize: PAGE_SIZE,
                searchValue: this.searchValueBy.date
            }
            const responseData = await this.$request.getRequest('RPT20500111', requestBody, true) as RPT202001SS08_RES_BODY;
            if (responseData.header.result) {
                this.schedulePaymentReportData = this.onMappingTableData(responseData.body.list);
                this.totalItems = responseData.body.totalCount
            }
        },

        onMappingTableData(lists) {
            return lists.map((item, index) => ({
                no: index + 1,
                referenceNo: item.referenceNo,
                customerNo: item.customerNo,
                customerName: item.customerName,
                fromAccountNo: item.fromAccountNo,
                numberOfLeaves: item.numberOfLeaves,
                feeCharge: item.feeCharge,
                remark: item.remark,
                authorizationStatus: item.authorizationStatus,
                createBy: item.createBy,
                numberOfChequeBook: item.numberOfChequeBook,
                createDate: item.createDate,
                authorizationBy: item.authorizationBy,
                authorizationDate: item.authorizationDate,
                authorizationStatusBackend: item.authorizationStatusBackend,
                verifiedBy: item.verifiedBy,
                verifiedDate: item.verifiedDate,
                authorizationByBackend: item.authorizationByBackend,
                authorizationDateBackend: item.authorizationDateBackend,
            }));
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.getOpenFixDepositReportData();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleSearch() {
            if (this.currentPage > 1) {
                this.currentPage = 1
                this.isResetPage = true;
                this.getOpenFixDepositReportData();
            } else {
                this.isResetPage = true;
                this.getOpenFixDepositReportData();
            }
        },

        handleReset() {
            this.searchValueBy.date = new Date()
            this.currentPage = 1
            this.isResetPage = true
            this.getOpenFixDepositReportData();
        }

    }
});
interface SchedulePaymentReportDataType {
    referenceNo: string;
    customerNo: string;
    customerName: string;
    numberOfLeaves: string;
    feeCharge: string;
    remark: string;
    authorizationStatus: string;
    createBy: string;
    createDate: string;
    authorizationBy: string;
    authorizationDate: string;
    authorizationStatusBankend: string;
    verifiedBy: string;
    verifiedDate: string;
    authorizationByBackend: string;
    AuthorizationBakend: string;
}

interface RPT202001SS08_REQ_BODY {
    pageNumber
    pageSize
    searchValue
}

interface RPT202001SS08_RES_BODY {
    header: Header
    body: {
        totalCount: number
        list: SchedulePaymentReportDataType[]
    }
}


